import * as Utils from 'utils/utils'

import web3accounts  from 'web3/packages/web3-eth-accounts'
// import WEB3    from 'web3/packages/web3'

import ethRPC  from 'API/RPC'



const wallet_password  = '1234'

let _config, _RPC
let _wallet = { openkey:false }

export default class Account {
	constructor(config, callback=false) {

		callback  = callback || (()=>{})
		
		_config   = config
		_RPC      = new ethRPC( config.rpc_url )
		// this.web3 = new WEB3(new WEB3.providers.HttpProvider(config.rpc_url))
		

		//
		// Init account 
		//
		const web3acc = web3accounts //this.web3.eth.accounts

		// Find wallet in localstorage
		if (localStorage && localStorage.web3wallet) {		
			try {
				_wallet.openkey = web3acc.recover(JSON.parse(localStorage.web3wallet).sign)
			} catch(e) { }
		}

		if (_wallet.openkey) {
			callback(); return
		}


		const newAcc = web3acc.create()

		localStorage.web3wallet = JSON.stringify({
			openkey   : newAcc.address,
			sign      : web3acc.sign(wallet_password, newAcc.privateKey)
		})		
		
		_wallet.openkey = newAcc.address

		callback()
	}


	get(){
		return _wallet
	}

	async info(address=false, callback=false){
		if (!callback && typeof address === 'function') {
			callback = address
			address = _wallet.address
		}
		
		address = address || _wallet.openkey

		const [bet, eth] = await Promise.all([
			this.getBetBalance( address ),
			this.getEthBalance( address )
		])

		const res = {
			openkey: address,
			balance: {
				eth:eth,
				bet:bet,
			}
		}

		if (callback) callback(res)
		return res
	}

	exportPrivateKey(password=false, callback){
		password = password || wallet_password
		const decrypt_key = this.web3.eth.accounts.decrypt(_wallet.privateKey, password)
		if (callback) callback(decrypt_key)
	}

	reset(){
		localStorage.web3wallet = false
	}

	
	sign(rawMSG){ }

	getBalance(address=false, callback=false){ }

	getEthBalance(address=false, callback=false){
		return new Promise((resolve, reject) => {
			let address = address || this.get().openkey
			
			_RPC.request('eth_getBalance', [address, 'latest']).then( response => {
				const balance = Utils.hexToNum(response.result) / 1000000000000000000
				resolve( balance )
				if(callback) callback( balance )
			}).catch( err => {
				console.error(err)
				reject(err)
			})
		})
	}


	getBetBalance(address=false, callback=false){
		return new Promise((resolve, reject) => {
			let address = address || this.get().openkey

			if (!address) {
				return
			} 

			let data = '0x' + Utils.hashName('balanceOf(address)')
					  		+ Utils.pad(Utils.numToHex(address.substr(2)), 64)
			_RPC.request('eth_call', [{
				'from' : this.get().openkey,
				'to'   : _config.erc20_address,
				'data' : data
			}, 'latest']
			).then( response => {
				const balance = Utils.hexToNum(response.result) / 100000000
				resolve( balance )
				if(callback) callback( balance )
			}).catch( err => {
				console.error(err)
				reject(balance)
			})
		})
	}

	sendBets(to, amount, callback){
		// Create contract function transaction
		this.signedContractFuncTx(
			// contract with bets
			_config.erc20_address, _config.erc20_abi,

			// contract function and params
			'transfer', [to, (amount*100000000)],
			0,
			// result: signed transaction
			signedTx => {

				// send transacriont to RPC
				_RPC.request('eth_sendRawTransaction', ['0x'+signedTx], 0).then( response => {
					if (!response || !response.result) { return }
					callback( response.result )
				})
			}
		)
	}

	sendEth(to, amount, callback){
		amount = amount * 1000000000000000000

		this.signedEthTx(to, amount, signedEthTx=>{
			_RPC.request('eth_sendRawTransaction', ['0x'+signedEthTx], 0).then( response => {
				if (!response || !response.result) { return }
				callback( response.result )
			})

		})
	}
    
	concatSig(VRS) {
		return VRS.r + VRS.s.substr(2) + VRS.v.substr(2)
	}


	signMsg(rawMsg, callback){
		this.exportPrivateKey((privateKey) => {
			let VRS = web3acc.sign(rawMsg, privateKey)
			const concat = this.concatSig(VRS)
			callback( concat )
		})
	}

	checkSIG(rawMsg, signature, author_address){
		let v = Utils.hexToNum(signature.slice(130, 132)) // 27 or 28
		let r = signature.slice(0, 66)
		let s = '0x' + signature.slice(66, 130)

		let msg_openkey = false

		try {
			msg_openkey = Utils.buf2bytes32( ethWallet.signing.recoverAddress(rawMsg, v, r, s) )
		} catch(e) {
			console.error('recoverAddress err:',e)
			return false
		}

		let ok = (msg_openkey==author_address)

		if (!ok) {
			console.error('invalid sig', msg_openkey+'!=='+author_address)
		};

		return ok
	}



	//  Make and Sing contract function transaction
	signedContractFuncTx(contract_address, function_name, args_type ,function_args, value=0, callback){

		this.getNonce( nonce => {

			let options = {
				to:       contract_address,
				data:     data,
				value:    value.toString(),
				nonce:    nonce.toString(),
				gasPrice: _config.gasPrice.toString(),
				gas: parseInt(_config.gasLimit),
			}

			//  Make contract function transaction
			// https://github.com/ConsenSys/eth-lightwallet#txutilsfunctiontxabi-functionname-args-txobject

			//  Sign transaction
			this.exportPrivateKey((privateKey) => {
				this.signTx(options, privateKey, callback)
			})                     
		})
	}

	// Make and Sing eth send transaction
	signedEthTx(to_address, value, callback){
		this.getNonce( nonce => {

			// https://github.com/ConsenSys/eth-lightwallet#txutilsvaluetxtxobject
			let options = {
				to:       to_address,
				value:    value.toString(),
				nonce:    nonce.toString(),
				gasPrice: _config.gasPrice.toString(),
				gas: parseInt(_config.gasLimit),
			}

			// Make transaction

            //  Sign transaction
			this.exportPrivateKey((privateKey) => {
				this.signTx(options, privateKey, callback)
			})
		})
	}

	// Sing transaction
	// https://github.com/ConsenSys/eth-lightwallet#signingsigntxkeystore-pwderivedkey-rawtx-signingaddress-hdpathstring
	signTx(tx, privateKey, callback){
		web3acc.signTransaction(tx, privateKey, callback)        
	}
}



